.ss-dashboard-wrap-main-header {
    font-family: "Montserrat", sans-serif;
    position: fixed;
    height: 82px;
    border-bottom: 1px solid @light-gray1;
    height: @main-header-height;
    top: 0;
    left: 0;
    right: 0;
    background-color: @white1;
    z-index: @main-header-zindex;

    > div {
        padding: 12px 36px;
        display: flex;
        width: 100%;
        height: @main-header-height;
        justify-content: space-between;
        align-items: center;
    }

    h1 {
        font-size: 20px;
        line-height: 20px;
        margin: 0;
    }
}

.ss-dashboard-wrap-user-top-nav {
    white-space: nowrap;
    margin-right: -8px;

    > * {
        vertical-align: middle;
    }
}

.ss-dashboard-wrap-user-name {
    font-family: "Montserrat", sans-serif;
    text-align: right;
    width: 50vw;
    max-width: 120px;
    padding-right: 8px;
    white-space: nowrap;

    .ss-dashboard-wrap-user-dd {
        line-height: 12px;
        cursor: pointer;
        > * {
            vertical-align: middle;
        }
    }
}

.ss-dashboard-wrap-dd-profile {
    min-width: 125px;
    overflow-x: auto;
    @media screen and (min-width: @screen-xs) {
        max-height: 320px;
    }

    a {
        font-family: "Montserrat", sans-serif;
        font-size: 12px;
        color: @black;
        width: 220px;
        display: block;
        white-space: nowrap;
        > * {
            vertical-align: middle;
            display: inline-block;
        }
        svg {
            margin-right: 8px;
        }
        span {
            text-overflow: ellipsis;
            overflow: hidden;
            width: calc(100% - 24px);
        }
    }
}

@media screen and (min-width: @screen-xl) {
    .ss-dashboard-wrap-user-top-nav .ss-dashboard-wrap-btn.menu-burger {
        display: none;
    }
}

@media screen and (min-width: @screen-md-min) {
    .ss-dashboard-wrap-mobile-name {
        display: none;
    }
}

@media screen and (max-width: @screen-sm-max) {
    .ss-dashboard-wrap-user-dd,
    .ss-dashboard-wrap-main-header h1,
    .ss-dashboard-wrap-user-top-nav .ss-dashboard-wrap-btn:not(:last-child) {
        display: none;
    }

    .ss-dashboard-wrap-main-header,
    .ss-dashboard-wrap-main-header > div {
        height: 52px;
    }
    .ss-dashboard-wrap-main-header {
        background: linear-gradient(90deg, #1268b3 30%, #01abec 100%);
    }

    .ss-dashboard-wrap-main-header {
        > div {
            display: block;
            padding: 12px;
        }
    }

    .ss-dashboard-wrap-user-top-nav {
        display: flex;
        align-items: center;
        flex-flow: row-reverse;
        justify-content: flex-end;
    }

    .ss-dashboard-wrap-user-top-nav .ss-dashboard-wrap-btn:last-child {
        padding: 0;
        margin-left: 0 !important;

        svg {
            height: 22px;
        }
    }

    .ss-dashboard-wrap-user-name {
        font-size: 20px;
        color: @white1;
        line-height: 20px;
        font-weight: 600;
        width: calc(100% - 40px);
        max-width: 100%;
        text-align: left;
        text-overflow: ellipsis;
        overflow: hidden;
    }
}

@media screen and (max-width: @screen-xs) {
    .ss-dashboard-wrap-user-name {
        font-size: 16px;
    }
}
